.hr-text {
  color: #cbd2d6;
  text-align: center;
  opacity: .5;
  border: 0;
  outline: 0;
  height: 1.5em;
  line-height: 1em;
  position: relative;
}

.hr-text:before {
  content: "";
  background: #818078;
  width: 100%;
  height: 1px;
  position: absolute;
  top: 50%;
  left: 0;
}

.hr-text:after {
  content: attr(data-content);
  color: #000;
  background-color: #fcfcfa;
  padding: 0 .5em;
  line-height: 1.5em;
  display: inline-block;
  position: relative;
}

